.terminal-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    max-width: 80%; /* allow for some responsiveness */
    height: 300px;
    background-color: #000;  /* Terminal-style background */
    color: #fff;             /* Terminal-style text color (bright green) */
    border: 2px solid #444;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    z-index: 9999; /* ensure it's on top of most elements */
    overflow-y: auto; /* in case there's lots of content */
  }
  
  .terminal-output {
    flex: 1; /* push the input area to the bottom */
    overflow-y: auto;
    font-family: 'Courier New', Courier, monospace;
    margin-bottom: 1rem;
  }
  
  .terminal-line {
    white-space: pre-wrap;
    margin-bottom: 0.25rem;
  }
  
  .terminal-input-area {
    display: flex;
    align-items: center;
  }
  
  .terminal-prompt {
    margin-right: 0.5rem;
  }
  
  .terminal-input {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    color: #fff;
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
  }